// 九宫格
export function getChannelList() {
  return request({
    url: '/home/catitems'
  })
}

export function getFloorList() {
  return request({
    url: '/home/floordata'
  })
}

// 商品分类
export function getCategoriesList() {
  return request({
    url: '/categories'
  })
}

// 商品列表
export function getGoodsList(params) {
  return request({
    method: 'get',
    url: '/goods/search',
    params
    // params: {
    //   pagenum
    // }
  })
}

// 根据id查询商品详情
export function getGoodsInfo(iid) {
  return request({
    url: '/goods/detail',
    params: {
      goods_id: iid
    }
  })
}

